<template>
  <div>
    <right-details-drawer v-if="store.state.keychain.detailsKey"/>
    <card class="card-plain">
      <paper-table
        type="hover"
        :data="table.data"
        :columns="table.columns"
        :maxRow="table.data.length"
      ></paper-table>
    </card>
  </div>
</template>

<script>
class Key {
  constructor(id, shared, keyName, access, status, sharing ) {
    this.id = id;
    this.shared = shared;
    this['key name'] = keyName;
    this.access = access;
    this.status = status;
    this.sharing = sharing;
  }
}
import { PaperTable } from "@/components";
import moment from "moment";
import repository from "@/repository/keychain";
import { keychainRepository } from "@/repository/keychain";
import RightDetailsDrawer from "@/layout/dashboard/RightDetailsDrawer";
import store from "@/flulux/store";
export default {
  components: {
    PaperTable,
    RightDetailsDrawer
  },
  data() {
    return {
      table: {
        title: "",
        columns: ["key name", "access", "status", "sharing"],
        data: []
      },
      store: store
    };
  },
  async mounted() {
    if (!sessionStorage.getItem("token")) {
      return this.$router.push("login");
    }
    await Promise.all([repository.getDoorLocks(), repository.getSharedK()]);
    await this.parseDataTable();
    this.store.actions.SET_NB_ROW_KEYCHAIN_TABLE(this.table.data.length);
    this.setMaxPagination(this.table.data.length);
  },
  methods: {
    setMaxPagination(p) {
      if (p <= 20) {
        this.store.actions.SET_END_PAGINATION_KEYCHAIN(p);
      } else this.store.actions.SET_END_PAGINATION_KEYCHAIN(20);
    },
    async parseDataTable() {
      keychainRepository.door_locks.map((i, index) => this.table.data.push(new Key(i.id, undefined, i.description, 'owner', i.existing_status, i.sharing)));
      keychainRepository.shared_key.map((i, index) => this.table.data.push(new Key(i.door_lock_id, true, i.door_lock.description, this.checkAccess(i.admin), this.checkStatus(i), i.sharing)))
    },

    getNbUsers(p) {
      if (p) return p;
      return 0;
    },

    async getNbUsersSharedKey(p, admin) {
      if (admin) return await repository.getAccessManagement(p);
      return [];
    },
    /**
     * return access status from param
     */
    checkAccess(p) {
      if (p) return "Admin";
      return "Normal";
    },
    /**
     * return key status from param
     */
    checkStatus(p) {
      if (!p.start_date && !p.end_date) return "unlimited";
      if (moment(p.start_date) > moment())
        return "active in " + this.getDiffTime(p.start_date, "start");
      if (p.end_date !== null && moment(p.end_date) < moment())
        return "inactive";
      if (p.end_date !== null && moment(p.end_date) > moment())
        return "end in " + this.getDiffTime(p.end_date, "end");
      else return p.existing_status;
    },
    /**
     * get difference time beetween two dates
     */
    getDiffTime(p) {
      return moment
        .duration(
          moment
            .utc(p)
            .local()
            .diff(moment.utc().local())
        )
        .humanize();
    }
  }
};
</script>

<style>
.card-body {
  overflow: -webkit-paged-x;
}
</style>
