<template>
  <div class="table-responsive">
    <table v-if="data.length > 0" class="table margin" :class="tableClass">
      <thead>
        <slot name="columns">
          <th
            :style="{zIndex: (15 + i)}"
            class="capitalize h paragraph-small-regular keychainTableWidth"
            v-for="(column, i) in columns"
            :key="column"
          >
            {{column}}
            <img class="filter-th" src="@/assets/havr-icon/filterTableIcon.svg">
          </th>
        </slot>
      </thead>

      <tbody>
        <tr
          v-for="(item, index) in data.slice(store.state.keychain.startPaginationItem - 1, store.state.keychain.endPaginationItem)"
          :key="index"
        >
          <slot :row="item">
            <td
              style="cursor: pointer;"
              @click="setDetailsItem(item)"
              class="margin-td capitalize"
              v-for="(column, index) in columns"
              :key="index"
            >
              <div class="container-table">
                <div class="valid" v-if="column === 'status' && item[column] === 'active'">
                  <i class="ti-check light"></i>
                </div>

                <div v-if="column === 'status' && item[column] === 'inactive'">
                  <i class="ti-close inactive"></i>
                </div>
                {{item[column]}}
              </div>
            </td>
          </slot>
        </tr>
        <div class="fixed-footer-separator"></div>
      </tbody>
    </table>
  </div>
</template>
<script>
import store from "@/flulux/store";
import lodash from "lodash";
import notifiy from "vue-notification";
import { keychainRepository } from "@/repository/keychain";
import { userDataRepository } from "@/repository/users";
import userRepository from "@/repository/users";
import repository from "@/repository/keychain";
export default {
  name: "paper-table",
  data() {
    return {
      store: store,
      userDataRepository: userDataRepository,
      notificationToReturn: [],
      accessManagement: {
        admin: [],
        users: []
      },
      cantClick: false
    };
  },
  props: {
    columns: Array,
    data: Array,
    type: {
      type: String, // striped | hover
      default: "striped"
    },
    maxRow: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ""
    },
    subTitle: {
      type: String,
      default: ""
    }
  },
  computed: {
    tableClass() {
      return `table-${this.type}`;
    }
  },
  methods: {
    hasValue(item, column) {
      return item[column.toLowerCase()] !== "undefined";
    },
    itemValue(item, column) {
      return item[column.toLowerCase()];
    },
    async setDetailsItem(p) {
      if (this.cantClick === true) {
        return;
      }
      this.allowClick(true);
      this.notificationToReturn = [];
      await this.store.actions.SET_ACTIVITY([]);
      await this.store.actions.SET_ACCESS_MANAGEMENT([]);
      this.accessManagement.admin = [];
      this.accessManagement.users = [];
      this.store.state.keychain.keylog = [];
      let obj = {};
      if (p.access === "Normal") {
        return this.$notify({
          message:
            "Details lock door is not available with " + p.access + " access",
          type: "danger",
          horizontalAlign: "right",
          timeout: 2000,
          verticalAlign: "top"
        });
      }
      if (p.status === "inactive") {
        return this.$notify({
          message:
            "Details lock door is not available with " + p.status + " status",
          type: "danger",
          timeout: 2000,
          horizontalAlign: "right",
          verticalAlign: "top"
        });
      }
      this.$notify({
        message: "Selected key : " + p["key name"],
        type: "info",
        timeout: 2000,
        horizontalAlign: "right",
        verticalAlign: "top"
      });

      if (p.shared) {
        obj = lodash.filter(keychainRepository.shared_key, {
          door_lock_id: p.id
        });
        obj[0].id = obj[0].door_lock_id;
        obj[0].lock_group_id = obj[0].door_lock.lock_group_id;
      } else {
        obj = lodash.filter(keychainRepository.door_locks, { id: p.id });
      }
      obj[0].lock_group_description = await this.getLockGroupFromId(
        obj[0].lock_group_id
      );

      const setDetailsKey = await this.store.actions.SET_DETAILS_KEYCHAIN(
        obj[0]
      );
      const accessManagement = await repository.getAccessManagement(obj[0].id);
      const parseAccessManagement = await this.getAccessManagement(
        accessManagement
      );
      const setAccessManagement = await this.store.actions.SET_ACCESS_MANAGEMENT(
        this.accessManagement
      );
      const logs = await repository.getLogs(obj[0].id);
      this.store.actions.OPEN_DETAILS_DRAWER(true);
      this.allowClick(false);
      if (keychainRepository.actvityKey.length === 0) {
        return this.store.actions.SET_ACTIVITY(false);
      }
      const parseLogs = await this.getLogs();
      this.store.actions.SET_ACTIVITY(this.notificationToReturn);
    },
    async getLogs() {
      const res = await Promise.all(
        keychainRepository.actvityKey.map(async i => {
          const resp = await this.buildLogs(i);
          if (resp) {
            this.notificationToReturn.push(resp);
          }
        })
      );
      return res;
    },
    allowClick(p) {
      console.log(this.cantClick, "click");
      this.cantClick = p;
    },
    async getAccessManagement(p) {
      const res = await Promise.all(
        p.map(async i => {
          const resp = await this.buildLogs(i);
          if (resp && resp.admin) {
            this.accessManagement.admin.push(resp);
          }
          if (resp && !resp.admin) {
            this.accessManagement.users.push(resp);
          }
        })
      );
      return res;
    },
    async getLockGroupFromId(p) {
      const res = await repository.getLockGroupFromId(p);
      if (res.parent_lock_group) {
        return res.parent_lock_group.description;
      }
      return res.description;
    },
     async buildLogs(i) {
      if (i.user_id) {
        i.fullName = undefined;
        let res = await userRepository.retrieveUser(i.user_id);
        if (res === false) {
          return;
        }
        const picture = lodash.filter(userDataRepository.contactPictures, {
          id: i.user_id
        });
        if (picture.length !== 0) {
          i.picture = picture[0].img;
        } else {
          const res = await userRepository.retrieveContactPicture(i.user_id);
          if (res !== false) {
            i.picture = res.base64;
          } else {
            i.picture = undefined;
          }
        }
        i.fullName = res.first_name + " " + res.last_name;
        return i;
      }
    }
  }
};
</script>
<style>
.margin-td {
  padding-bottom: 20px !important;
  padding-top: 20px !important;
}
.table > tbody > tr {
  position: relative;
}
.capitalize {
  text-transform: capitalize;
}
.table-hover tbody tr:hover {
  background-color: #e6e6e6 !important;
}
.grey {
  color: #e6e6e6;
}
.valid {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: #dcdcdc;
  position: relative;
  top: 2px;
}
.ti-check:before {
  content: "\E64C";
  font-size: x-small;
  position: absolute;
  left: 2px;
  top: 3px;
}
.container-table {
  display: flex;
}
.light {
  color: white;
}
.margin {
  position: relative;
  top: 8em;
}
.filter-th {
  margin-left: 25px;
}
.inactive {
  background: #e74c3c;
  border-radius: 10px;
  color: white;
}
.fixed-footer-separator {
  height: 20px;
}

.keychainTableWidth {
  width: 25%;
}

thead {
  position: fixed;
  z-index: 999;
  background: white;
  display: table;
  width: calc(100% - 260px);
}
</style>